<template>
  <div class="login-div my-container" v-bind:style="'background-image:url('+background_image+')'">
    <div class="input-group login-center mborder">
      <img class="big-logo mb-2" src="./navigationbar/logo.png" />
      <br />
      <h1>Memo</h1>
      <h5 class="slogan">{{translate('slogan')}}</h5>
      <div v-if="isLoading">
          <general-loading></general-loading>
      </div>
      <!-- <button class="btn mborder" @click="login">
        <img
          width="20px"
          alt="Google Logo"
          src="https://upload.wikimedia.org/wikipedia/commons/thumb/5/53/Google_%22G%22_Logo.svg/512px-Google_%22G%22_Logo.svg.png"
        />
        ng nhp bng ti khon Google
      </button>-->
      <div v-else>
      <GoogleLogin :params="params" :onSuccess="onSuccess" :onFailure="onFailure" class="btn mborder btn-white">
        <img
          width="20px"
          alt="Google Logo"
          src="https://upload.wikimedia.org/wikipedia/commons/thumb/5/53/Google_%22G%22_Logo.svg/512px-Google_%22G%22_Logo.svg.png"
        />
        {{translate('login_with_google')}}
      </GoogleLogin>
      </div>
    </div>
  </div>
</template>

<script>
import GoogleLogin from "vue-google-login";
import userRepository from "../repositories/userRepository"
import GeneralLoading from "../components/shared/GeneralLoading"
import { translate } from './shared/translator';
import { getCookie } from '../repositories/cookieRepository';
import { EventBus } from './shared/event-bus';
import { errorMessages } from './shared/errorMessage';
export default {
  props:{
    lang:{
      type:String,
      default:"en"
    }
  },
  async created(){
    let uid = getCookie("_id");
    let jhash = getCookie("jhash");
    console.log("id :", uid, " hash :", jhash);
    if (uid == null || jhash == null || uid == undefined || jhash == undefined)
      this.isLoading = false;
    else {
      this.isLoading = true;
      try {
        let userProfile = await userRepository.getProfile(uid);
        this.$emit("logged_in",userProfile);
        // console.log(userProfile);
        // this.user = userProfile;
      } catch (e) {
        //TODO hin th thng bo khng th ng nhp
        EventBus.$emit("showError",errorMessages[`${e}`])
        console.log(e);
      }finally{
        // this.isLoged = true;
        // this.isLoading = false;
      }
    }
  },
  data() {
    return {
      isLoading:false,
      params: {
        client_id: "571858267778-fa8fu87hpjoel1svuevnpjm94s4oeev2.apps.googleusercontent.com",
        client_secret:"AybaKEwNFbTVIXchlvSPRqHv"
      },
      background_image:
        "https://images.unsplash.com/photo-1471107340929-a87cd0f5b5f3?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=966&q=80"
    };
  },
  methods: {
    translate:translate,
    async onSuccess(googleUser) {
      console.log("google user Auth",googleUser.isSignedIn());
      if(googleUser.isSignedIn()){
        console.log("ac token ", googleUser.getAuthResponse(true).access_token);
        try {
          this.isLoading=true;
          let result = await userRepository.loginWithGoogle(googleUser.getAuthResponse(true).access_token);
          this.$emit("logged_in",result)
        } catch (e) {
          console.error(e);
        EventBus.$emit("showError",errorMessages[`${e}`])

        } finally{
          // this.isLoading=false;
        }
      }
      console.log("google user",googleUser);
      // This only gets the user information: id, name, imageUrl a
    },
    onFailure(fail){
        console.log("fail:",fail);
    }
  },
  components: {
    GoogleLogin,
    GeneralLoading
  }
};
</script>

<style>
.big-logo {
  width: 100px;
}
.my-container {
  height: 100vh;
  background-position: center; /* Center the image */
  background-repeat: no-repeat;
  background-size: cover;
  text-align: center;
}
.slogan {
  font-style: italic;
}
.login-center {
  background-color: rgba(255, 255, 255, 0.85);
  display: inline-block;
  position: relative;
  top: 30%;
  width: 400px;
  margin: 0 auto;
  padding: 50px;
  z-index: 99;
}
.btn-white{
  background-color: rgb(255, 255, 255)
}
</style>